body {
  background-color: #f5f5f5;
  flex-direction: column;
  align-items: center;
  margin: 0;
  padding: 20px;
  font-family: Arial, sans-serif;
  display: flex;
}

h1 {
  margin-bottom: 20px;
  font-size: 24px;
}

label {
  margin-bottom: 10px;
  font-size: 16px;
  display: block;
}

.search-input {
  width: 100%;
  max-width: 300px;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-bottom: 10px;
  padding: 10px;
  font-size: 16px;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .1);
}

.breed-list {
  width: 100%;
  max-width: 300px;
  max-height: 30vh;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-top: 5px;
  padding: 0;
  list-style: none;
  display: none;
  overflow-y: auto;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .1);
}

.breed-list li {
  cursor: pointer;
  padding: 10px;
}

.breed-list li:hover {
  background-color: #f0f0f0;
}

.loader, .error {
  color: #d9534f;
  margin: 20px 0;
  display: none;
}

.loader {
  color: #5bc0de;
}

.cat-info {
  width: 100%;
  max-width: 600px;
  background: rgba(255, 255, 255, .8);
  border: 1px solid #ddd;
  border-radius: 4px;
  margin-top: 20px;
  padding: 20px;
  display: none;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .1);
}

.cat-info img {
  max-width: 100%;
